import { apiBaseUrl } from '@/config/config.js';
export default {
	name: "jshoprecord",
	props: {
		jdata:{
			// type: Object,
			required: true,
		},
		//记录显示的位置类型
		ltype:{
			type: String,
			required: false,
			default:'home',
		},
		//记录显示的位置的值
		lvalue:{
			type: String,
			required: false,
			default:'0',
		}
	},
    data() {
        return { 
			adbshow:false,
			hideanimation: true,
			log:{
				avatar:'/static/demo-img/user-head.jpg',
				nickname:'',
				desc:'',
				ctime:'',
			},
			times:{},//定时器
		};
    },
	methods:{
		//隐藏
		hideLog(){
			var _this = this;	
			_this.times = setInterval(function(){
				_this.adbshow = !_this.adbshow;
				_this.hideanimation = !_this.hideanimation;
				clearInterval(_this.times);
				_this.times = setInterval(function(){
					_this.getRecod();
				},5000);
			},3000)
		},
		//获取日志
		getRecod(){
			var _this = this;
			if(_this.times !={}){
				clearInterval(_this.times);
			}
			var data = {
				type:_this.ltype,
				value:_this.lvalue,
				method:'pages.getrecod',
			};
			uni.request({
				url: apiBaseUrl+'api.html',
				data: data,
				header: {
					'Accept': 'application/json',
					'Content-Type': 'application/json',
				},
				method: 'POST',
				success: (response) => {
					var res = response.data;
					if(res.status == true){
						if(res.data){
							_this.log = res.data;
							_this.adbshow = true;
							_this.hideanimation = false;
							_this.hideLog();
						}
					}
				}
			});
		}
	},
	mounted() {
		this.getRecod();
	}
}
